import axios from 'axios';

class WebService {
    static getEvents(date) {
        return new Promise((resolve, reject) => {
            axios.get('https://jsonp.afeld.me/?url=https://www.sofascore.com/ice-hockey/usa/'+date+'/json').then(function functionName(res) {
              const data = res.data.sportItem.tournaments.filter(x => {return x.tournament.id === 142;});
              resolve(
                  data[0] ? data[0].events.filter(x => {return x.formatedStartDate === date.split('-').reverse().join('.')+'.'}) : [{name: 'There is no events for this date , please select another date',customId:'error'}]
              );
            }).catch ((err)=>{
              reject(err)
            })
        });
    }
    static insertGame(game) {
      return new Promise((resolve, reject) => {
            axios.post('https://www.nhlstreams100.com/wp-json/addp/124', {
              game: game
            }).then(function(res) {
              resolve(res.data)
            }).catch((err)=>{
                reject(err)
            })
      });
    }
}

export default WebService;
