<template>
  <table>
     <tbody>
       <tr v-for="item in events" v-bind:key="item.customId">
         <td v-if="item.customId === 'error'" style="text-align: center">{{item.name}}</td>
         <td v-if="item.customId !== 'error'">{{item.name}}</td>
         <td v-if="item.customId !== 'error'">
           <span v-if="!submitted.includes(item.customId)" class="sub" v-on:click="submit(item)">Submit</span>
           <span v-if="submitted.includes(item.customId)" class="n sub">Submitted
             <button v-on:click="clear(item.customId)">
               <span>&#10007;</span>
             </button>
           </span>
         </td>
       </tr>
     </tbody>
  </table>
</template>

<script>
import WebService from "../WebService";

export default {
  name: 'Events',
  data (){
    return {
      submitted: [],
      error: ''
    }
  },
  props: {
    events: {
      type: Array
    }
  },
  mounted() {
    if (localStorage.getItem('submitted'))
    this.submitted = JSON.parse(localStorage.getItem('submitted'));
  },
  methods:{
    submit:function (item) {
      WebService.insertGame(item).then(() =>{
        this.submitted.push(item.customId);
        localStorage.setItem('submitted', JSON.stringify(this.submitted));
      }).catch(err=>{
        this.error = err.message;
        alert('there is an error please try again');
      })
    },
    clear:function (item) {
      this.submitted.splice(this.submitted.indexOf(item),1);
      localStorage.setItem('submitted', JSON.stringify(this.submitted));
    }
  }
}
</script>
<style scoped>
button{
  right: 2px;
  top: 2px;
  width: 28px;
  height: 28px;
  cursor: pointer;
}
</style>
